import React from 'react';
import { TrainingContext } from './RunTraining';


const CurrentTrainingInfo = () => {
  let trainingContext = React.useContext(TrainingContext);
  let [trainingState, setTrainingState] = trainingContext;

  console.log("CURRNET TRAINING INFO running")
  return (
    <div>
      <h1>Current training info:</h1>
      <ul>{trainingState.main.training.sets.map((set, index) =>
        <li key={index}>
          Set {index + 1}: {set.reps ? `${set.reps} reps` : `${set.duration} seconds`}
          <br/>
          Exercise Notes: {set.repsNotes}
          <br/>
          Break after: {set.breakAfterInSeconds}s.
          <br/>
          Break Notes: {set.breakNotes}

        </li>)}
      </ul>
      {/* <button onClick={() => {
        trainingState.stage++;
        setTrainingState({ ...trainingState })
      }}>Start</button> */}
      {/* <button onClick={()=>{setTrainingState({...trainingState,stage: (++trainingState.stage)} )}}>Start</button> */}
      <button onClick={()=>{setTrainingState({...trainingState, stage: (trainingState.stage+1)} )}}>Start</button>
    </div>
  );
};

export default CurrentTrainingInfo;